@use "sass:map";
$SoftRed: hsl(7, 99%, 70%) ;
$Yellow: hsl(51, 100%, 49%);
$DarkDesaturatedCyan: hsl(167, 40%, 24%) ;
$DarkBlue : hsl(198, 62%, 26%) ;
$DarkModerateCyan: hsl(168, 34%, 41%);
$VeryDarkDesaturatedBlue: hsl(212, 27%, 19%);
$VeryDarkGrayishBlue: hsl(213, 9%, 39%) ;
$DarkGrayishBlue: hsl(232, 10%, 55%);
$GrayishBlue: hsl(210, 4%, 67%) ;
$White: hsl(0, 0%, 100%);
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:opsz,wght@9..144,700;9..144,900&display=swap');
$Barlow:'Barlow',sans-serif;
$Fraunces:'Fraunces',serif;
.family-barlow{
    font-family: $Barlow;
}
.family-fraunces{
    font-family: $Fraunces;
}
@mixin desktop {
    @media screen and (min-width:1024px) {
        @content;
    }
}
@mixin  flex($direction:column,$justify:center,$align:center) {
    display: flex;
    flex-direction:$direction;
    justify-content: $justify;
    align-items: $align;
}
@mixin bg($url,$position,$size:cover) {
    background-image: url($url);
    background-repeat: no-repeat;
    background-size: $size;
    background-position: $position;
}
$font-weights: (
    "semi-bold": 600,
    "bold": 700,
    "black": 900
);
@each $name,$weight in $font-weights{
    .fw-#{$name}{
        font-weight: $weight;
    }
};
$font-sizes:(
    "100":0.75rem,
    "200":0.875rem,
    "300":1rem,
    "400":1.125rem,
    "500":1.25rem,
    "600":1.5rem,
    "700":1.75rem,
    "800":2rem,
    "900":2.5rem,
    '1000':3.5rem
);
@each $key,$size in $font-sizes{
    .fs-#{$key}{
  font-size: $size;
    }
}
.fs-body{
    font-size: map-get($map: $font-sizes, $key:'300' );
    @include desktop{
        font-size: map-get($map: $font-sizes, $key: '400');
    }
}
.fs-txt{
    font-size: map-get($map: $font-sizes, $key: '200');
        @include desktop {
            font-size: map-get($map: $font-sizes, $key: '300');
        }
}
.fs-nav{
    font-size: map-get($map: $font-sizes, $key: '500');
    @include desktop{
font-size: map-get($map: $font-sizes, $key: '400');
    }
}
.fs-heading-primary{
font-size: map-get($map: $font-sizes, $key: '900');
@include desktop{
    font-size: map-get($map: $font-sizes, $key: '1000');
}
}
.fs-heading-secondary{
font-size: map-get($map: $font-sizes, $key: '700');
@include desktop{
    font-size: map-get($map: $font-sizes, $key: '800');
}
}
.fs-heading-medium{
font-size: map-get($map: $font-sizes, $key: '700');
@include desktop{
    font-size: map-get($map: $font-sizes, $key: '800');
}
}
.fs-heading-light{
    font-size: map-get($map: $font-sizes, $key: '500');
    
        @include desktop {
            font-size: map-get($map: $font-sizes, $key: '600');
        }
}
.fs-heading-small{
    font-size: map-get($map: $font-sizes, $key: '300');
    @include desktop {
            font-size: map-get($map: $font-sizes, $key: '400');
        }
}
.fs-heading-mini{
    font-size: map-get($map: $font-sizes, $key: '100');
    @include desktop {
            font-size: map-get($map: $font-sizes, $key: '200');
        }
}

.txt-gray-light{
    color: $GrayishBlue!important;
}
.txt-gray-mid{
    color: $DarkGrayishBlue!important;
}
.txt-gray-dark{
    color: $VeryDarkGrayishBlue !important;
}
.txt-cyan-light{
    color: $DarkModerateCyan !important;
}
.txt-cyan-dark{
    color: $DarkDesaturatedCyan !important;
}
.txt-blue{
    color: $DarkBlue !important;
}
.txt-blue-dark{
    color: $VeryDarkDesaturatedBlue!important;
}
.txt-white{
    color: $White;
}
.bg-yellow{
background-color: $Yellow;
}
.bg-red{
background-color: $SoftRed;
}
.bg-cyan{
    background-color: $DarkModerateCyan;
}
.bg-cyan-dark{
    background-color: $DarkDesaturatedCyan;
}

.flexCol{
    @include flex();
    text-align:center;
    gap: 1.5rem;
    @include desktop{
  @include flex(column,center,flex-start);
text-align: left;
gap:2.5rem;
    }
}
