@use  './styles' as c;

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: inherit;
}
body{
  font-family: c.$Barlow;
  line-height: 1.5;
}
a{
  text-decoration: none;
  display: block;
}
img{
  height: 100%;
  width: 100%;
}
h1,h2,h3,h4,h5{
  font-family: c.$Fraunces;
}
button{
  background-color: transparent;
  outline: none;
  border: none;
}
.header{
@include c.bg('./images/mobile/image-header.jpg',center);
@include c.desktop{
  @include c.bg('./images/desktop/image-header.jpg',bottom);
  height: 100vh;
}
padding-bottom: 5rem;
&--main{
  @include c.flex();
  gap: 2rem;
  @include c.desktop{
    gap: 5rem;
  }
}
&--image{
  height: 10vh;
  width: 7vw;
  @include c.desktop{
    height: 15vh;
    width: 4vw;
  }
}
&--heading{
  text-transform: uppercase;
  word-spacing: 1rem;
}
}

.nav{
  width: 100%;
  padding:1.5rem ;
  position:relative;
  margin-bottom: 3rem;
  @include c.desktop{
    padding-inline: 3rem;
    padding-block:0rem;
    margin-bottom: 5rem;
  }
  &--container{
    @include c.flex(row,space-between,center);
  };
  &--links{
    position: absolute;
    width:calc(100% - 3rem);
    @include c.flex();
   bottom:-270px;
   color: c.$GrayishBlue;
   background-color: white;
   padding-block: 2rem;
   gap: 1.5rem;
   @include c.desktop{
    position: static;
    background-color: transparent;
    display: flex!important;
    flex-direction: row;
    justify-content: flex-end;
color: white;
gap: 2rem;
   }
  }
  &--contact{
    padding: 1rem 1.5rem;
    background-color: c.$Yellow;
    color: black;
    text-transform: uppercase;
    font-family: c.$Fraunces;
    border-radius: 50px;
    @include c.desktop{
      background-color: white;
      &:hover{
   background-color: rgba($color:white, $alpha: 0.2);
   color: white;

      }
    }
  }
  &--btnOpen{
    @include c.desktop{
      display: none;
    }
  }
  &--btnClose{
    @include c.desktop{
      display: none;
    }
  }

}
.section{
  @include c.flex();
  @include c.desktop{
   display: grid;
grid-template-columns: repeat(2,1fr);
  }
&--bg{
height: 30vh;
  width: 100%;
    @include c.desktop {
height: 100%;
      }
}
  &--main{
    padding: 2rem 1rem;
    @include c.desktop {
       padding:10rem;
       &*{
        text-align: left;
       }
      }
  }
  &--link{
    text-transform: uppercase;
    font-family: c.$Fraunces;
    position: relative;
    font-size: 1.125rem;
    @include c.desktop{
      font-size: 1.25rem;
    }
    &::before{
      z-index: -5;
      content: '';
      position: absolute;
      height: 40%;
      width: 100%;
      bottom: 0;
      left: 0;
      border-radius:20px ;
    }
  }
}
.transform{
&--bg{
    @include c.bg('./images/mobile/image-transform.jpg',center);
    @include c.desktop{
          @include c.bg('./images/desktop/image-transform.jpg', center);
            order: 2;
    }
  }
  &--link{
    &::before{
        background-color: rgba($color: c.$Yellow, $alpha: 0.4);
    }

    &:hover{
        &::before {
            background-color: rgba($color: c.$Yellow, $alpha:1);
          }
    }
  }
}
.standOut{
  &--bg {
      @include c.bg('./images/mobile/image-stand-out.jpg', center);
      @include c.desktop {
          @include c.bg('./images/desktop/image-stand-out.jpg', center);
        }
    }
         &--link {
           &::before {
             background-color: rgba($color: c.$SoftRed, $alpha: 0.4);
           }
                     &:hover {
                       &::before {
                         background-color: rgba($color: c.$SoftRed, $alpha: 1);
                       }
                     }
         }
}
.articles{
    @include c.desktop {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
      }
}
.article{
  @include c.flex();
  gap: 1.5rem;
  text-align: center;
  padding-inline: 1rem;
  padding-bottom: 2rem;
  padding-top: 20rem;
  @include c.desktop{
    padding-top: 25rem;
    padding-inline: 10rem;
  }
  &--graphic{
    @include c.bg('./images/mobile/image-graphic-design.jpg',center);
    @include c.desktop{
        @include c.bg('./images/desktop/image-graphic-design.jpg',center);
    }
  }
  &--photography{
    @include c.bg('./images/mobile/image-photography.jpg',center);
      @include c.desktop {
          @include c.bg('./images/desktop/image-photography.jpg', center);
        }
  }
}

.testimonials{
  padding: 1rem;
  @include c.desktop{
    padding-block: 7rem;
    padding-inline: 10rem;
  }
  &--display{
  @include c.desktop {
      display: grid;
      grid-template-columns: repeat(3,1fr);
      gap: 3rem;
      align-items: flex-start;
    }
  }

  &--heading{
    margin-bottom: 2rem;
    text-transform: uppercase;
    word-spacing: 0.5rem;
    @include c.desktop{
      margin-bottom: 5rem;
    }
  }
}

.testimonial{
  @include c.flex();
  gap: 1rem;
  margin-bottom: 2rem;
  @include c.desktop{
    gap: 2rem;
  }
  &--bottom{
    @include c.flex();
    gap:0.5rem;
  }
&--image{
  height: 50px;
  width: 50px;
  border-radius: 50%;
  @include c.desktop{
    height: 70px;
      width: 70px;
  }
  img{
    border-radius: 50%;
  }

}

}
.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  @include c.desktop{
grid-template-columns: repeat(4, 1fr);
  }

  &--img {
height: 20vh;
    width: 100%;
    @include c.desktop{
      height: 50vh;
    }
    
  }

  &--milk {
    @include c.bg('./images/mobile/image-gallery-milkbottles.jpg', center);

@include c.desktop {
      @include c.bg('./images/desktop/image-gallery-milkbottles.jpg', center);
    }
  }

  &--orange {
    @include c.bg('./images/mobile/image-gallery-orange.jpg', center);

    @include c.desktop {
      @include c.bg('./images/desktop/image-gallery-orange.jpg', center);
    }
  }

  &--cone {
    @include c.bg('./images/mobile/image-gallery-cone.jpg', center);

    @include c.desktop {
      @include c.bg('./images/desktop/image-gallery-cone.jpg', center);
    }
  }

  &--sugar-cubes {
    @include c.bg('./images/mobile/image-gallery-sugar-cubes.jpg', center);

    @include c.desktop {
      @include c.bg('./images/desktop/image-gallery-sugarcubes.jpg', center);
    }
  }
}
.footer{
 @include c.flex();
padding-block: 2rem 3rem;
@include c.desktop{
  padding-block: 3rem;
}
&--container{
@include c.flex();
width: max-content;
}
&--logo{
  margin-bottom: 2rem;
}
&--links{
  @include c.flex(row);
gap: 4rem;
margin-bottom: 3rem
}
&--link{
  &:hover{
    color: white;
  }
}
&--socials{
    @include c.flex(row);
      gap: 2rem;
      i{
        transform: scale(1.4);
     &:hover{
      color: white;
     }
      }
}
}
.hide{
  display: none;
}
.txtCtr{
  text-align: center;
}
